<template class="student_list">
  <div>
    <Form inline>
            <FormItem>
              <Input
                v-model="something"
                placeholder="Enter something..."
                clearable
              />
            </FormItem>
            <FormItem>
              <Select
                v-model="education"
                clearable
                style="width:150px"
                placeholder="请选择学生类型"
              >
                <Option
                  v-for="item in educationList"
                  :value="item.value"
                  :key="item.value"
                >{{ item.label }}</Option>
              </Select>
            </FormItem>
            <FormItem>
              <Select
                v-model="status"
                clearable
                style="width:150px"
                placeholder="请选择学生学制"
              >
                <Option
                  v-for="item in statusList"
                  :value="item.value"
                  :key="item.value"
                >{{ item.label }}</Option>
              </Select>
            </FormItem>
            <FormItem>
              <Select
                v-model="status"
                clearable
                style="width:150px"
                placeholder="请选择学生年级"
              >
                <Option
                  v-for="item in statusList"
                  :value="item.value"
                  :key="item.value"
                >{{ item.label }}</Option>
              </Select>
            </FormItem>
            <FormItem>
              <DatePicker
                type="daterange"
                split-panels
                placeholder="开始时间 ~ 结束时间"
                style="width: 200px"
                v-model="data"
              ></DatePicker>
            </FormItem>
            <FormItem>
              <Button
                type="primary"
                icon="ios-search"
              >Search</Button> 
            </FormItem>
            <FormItem>
              <Button
                type="primary"
                icon="ios-search"
              >数据导入</Button> 
            </FormItem>
            <FormItem>
              <Button
                type="primary"
                icon="ios-search"
              >数据导出</Button> 
            </FormItem>
    
          </Form>
    <h1>设计一：每个学生的信息放在一块卡片上，可以展示出更多信息</h1>
    <div v-for="student in students" :key="student.id">
      <Card :key="student.id">
        <div class="stu_info">
          <div class="avatar">
            <Avatar :style="{ background: color }" size="large">{{
              student.name
            }}</Avatar>
            <p>
              姓名:<b>{{ student.name }}</b>
            </p>
            <p>学号:{{ student.id }}</p>
            <p>类型:{{ student.stu_type }}</p>
            <p>学制:{{ student.study_year }}</p>
          </div>
          <div>
            <Steps :current="1">
              <Step title="已完成" content="这里是该步骤的描述信息"></Step>
              <Step title="进行中" content="这里是该步骤的描述信息"></Step>
              <Step title="待进行" content="这里是该步骤的描述信息"></Step>
              <Step title="待进行" content="这里是该步骤的描述信息"></Step>
              <Step title="待进行" content="这里是该步骤的描述信息"></Step>
              <Step title="待进行" content="这里是该步骤的描述信息"></Step>
              <Step title="待进行" content="这里是该步骤的描述信息"></Step>
              <Step title="待进行" content="这里是该步骤的描述信息"></Step>
              <Step title="待进行" content="这里是该步骤的描述信息"></Step>
            </Steps>
          </div>
        </div>
      </Card>
      <br />
    </div>
    <br />
    <h1>设计二：学生信息由表格展示，更为紧凑</h1>
    <Table :columns="stu_info" :data="students"></Table>
  </div>
</template>
<script>
export default {
  name: "StudentList",
  data() {
    return {
      stu_info: [
        {
          title: "姓名",
          key: "name",
          width: 100,
        },
        {
          title: "学号",
          key: "id",
          width: 100,
        },
        {
          title: "类型",
          key: "stu_type",
          width: 100,
        },
        {
          title: "学制",
          key: "study_year",
          width: 100,
        },
        {
          title: "毕业进度",
          key: "progress",
          render: (h, params) => {// eslint-disable-line no-unused-vars
            
            return h(
              "Steps",
              {
                props: {
                  current: "1",
                },
              },
              [
                h("Step", {
                  props: {
                    title: "已完成",
                    content: "这里是该步骤的描述信息",
                  },
                }),
                h("Step", {
                  props: {
                    title: "进行中",
                    content: "这里是该步骤的描述信息",
                  },
                }),
                h("Step", {
                  props: {
                    title: "待完成",
                    content: "这里是该步骤的描述信息",
                  },
                }),
                h("Step", {
                  props: {
                    title: "待完成",
                    content: "这里是该步骤的描述信息",
                  },
                }),
                h("Step", {
                  props: {
                    title: "待完成",
                    content: "这里是该步骤的描述信息",
                  },
                }),
                h("Step", {
                  props: {
                    title: "待完成",
                    content: "这里是该步骤的描述信息",
                  },
                }),
              ]
            );
          },
        },
      ],
      students: [
        {
          name: "吴青峰",
          stu_type: "学硕",
          study_year: "3",
          longgest_year: "5",
          id: "11321",
        },
        {
          name: "吴青峰",
          stu_type: "学硕",
          study_year: "3",
          longgest_year: "5",
          id: "11322",
        },
        {
          name: "吴青峰",
          stu_type: "学硕",
          study_year: "3",
          longgest_year: "5",
          id: "11323",
        },
        {
          name: "吴青峰",
          stu_type: "学硕",
          study_year: "3",
          longgest_year: "5",
          id: "11324",
        },
        {
          name: "吴青峰",
          stu_type: "学硕",
          study_year: "3",
          longgest_year: "5",
          id: "11325",
        },
      ],
    };
  },
};
</script>
<style scoped>

.stu_info{
    display: flex;
    direction: inherit;
    margin-left: 10px;
    align-items: center;     /* 垂直居中 */
}
.avatar{
  width: 12.5%;
}
</style>
